body {
  margin: 0;
  padding: 0;
  font-family: 'Satisfy', cursive;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.app {
  margin: 0;
  padding: 0;
  background-image: url(../../static/media/hogwarts-castle-fog.f69c832b.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed; }

.app__header {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-bottom: 50px; }

.app__title {
  margin: 0;
  padding: 50px 0;
  text-align: center;
  text-transform: uppercase; }

.app__filter {
  text-align: center; }

.app__filter-name {
  outline: none; }

.app__main {
  display: flex;
  justify-content: center; }

.app__list {
  margin: 0 auto 20px;
  padding: 0;
  width: 100%;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(auto-fill, 202px);
  grid-gap: 20px;
  justify-content: center; }

.app__list-character {
  border: 1px solid black;
  background-color: rgba(71, 65, 65, 0.575);
  border-radius: 10px; }

.app__list-link {
  text-decoration: none; }

.character {
  color: white; }

.character__image {
  width: 200px;
  height: 250px;
  border-top-left-radius: 8px;
  border-top-right-radius: 8px; }

.character__name {
  text-align: center; }

.character__house {
  text-align: center; }

.container__character--detail {
  display: flex;
  flex-direction: column; }

.character--detail {
  display: grid;
  width: 500px;
  height: 350px;
  display: flex;
  border: 1px solid black;
  background-color: rgba(71, 65, 65, 0.575);
  border-radius: 10px;
  margin: 0 0 30px 0;
  color: white; }

.character__image--detail {
  width: 250px;
  height: 350px;
  border-top-left-radius: 9px;
  border-bottom-left-radius: 9px; }

.character__items--detail {
  margin-left: 20px; }

.go__back {
  color: white;
  font-weight: 550;
  text-transform: capitalize;
  text-decoration: none;
  text-align: center;
  padding: 5px 0;
  margin-bottom: 80px;
  width: 200px; }

.footer {
  width: 100%;
  display: flex;
  justify-content: center;
  background-color: #33576b; }

.footer__text {
  text-transform: uppercase;
  color: white; }

